.profile-container {
    text-align: center;
    width: 100vw;
    height: 100vh;
    color: white;
}

.profile-parent {
    width: fit-content;
    height: fit-content;
    margin: auto;
    display: flex;
    align-items: center;
    font-family: 'Uchen', serif;
}

.profile-container > h1 {
    padding-top: 100px;
    margin-bottom: 10rem;
    font-size: 50px;
    color: white;
    text-align: center;
    font-family: 'Fredoka One', cursive;
}

.profile-details {
    margin: 14px 0 24px 30px;
    width: 30rem;
    height: 15rem !important;
    text-align: left;
    font-size: 1.1rem;
}


.profile-picture {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    height: 380px;
    width: 380px;
}

.profile-picture-background {
    height: 92%;
    width: 92%;
    background-image: url('https://media-exp1.licdn.com/dms/image/C4E03AQGwUY6oTdpDGg/profile-displayphoto-shrink_800_800/0/1586207426822?e=1665014400&v=beta&t=NAsHP3p9uA-b_P3YkEnCaneAMzPYs7gZUoqrQgJxDMA');
    background-size: cover;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: center;
}

.profile-picture-background:hover {
    transform: scale(1.07);
    transition: 1s ease-out;
}



